.pageWrapper {
  width: 100%;
  height: 93vh;
}

.pageContentWrapper {
  display: flex;
  flex-flow: column;
  flex-wrap: wrap;
  padding: 28px 25px;
  width: 100%;
  height: 84.1vh;
  overflow: scroll;
  position: relative;
}

.outerCardWrapper {
  width: 100%;
  height: 100px;
  margin-bottom: 15px;
}

.cardWrapper {
  min-height: 100px;
  background: #f9fafb;
  border: 1px solid #e8ebef;
  border-radius: 10px;
  align-items: center;
  position: relative;
  overflow: hidden;
  transition: all 300ms ease-in-out;
  z-index: 0;
}

.cardWrapper:hover,
.cardWrapperSelected {
  box-shadow: 0 5px 30px rgba(118, 128, 146, 0.3);
  border: 1px solid rgba(118, 128, 147, 0.7);
  transition: all 600ms cubic-bezier(0.23, 1, 0.32, 1);
  z-index: 1;
}

.cardWrapperSettingsLoaded {
  height: 80vh;
}

.pageHeaderWrapper {
  display: inline-flex;
  width: 83%;
  justify-content: space-between;
}

.displayMasterNode {
  font-family: "Open Sans Bold", sans-serif;
  font-size: 14px;
  color: #768093;
}

.displayMasterNode input {
  margin-left: 5px;
}
