.filtersGroupWrapper {
  border: 1px solid #e8ebef;
  border-radius: 20px;
  font-size: 12px;
  color: rgb(118, 128, 147);
  display: inline-block;
  font-family: "Open Sans normal", sans-serif;
  height: 40px;
  padding: 0 10px;
  cursor: pointer;
  display: flex;
}

.filterWrapper {
  padding: 0 10px;
  line-height: 38px;
  text-align: center;
  border-right: 1px solid #e8ebef;
}

.filterWrapper:last-child {
  border-right: 0;
}
